      description: '<p>Talk:</p> <p>Meet NestJS with Ely Lucas</p> <p>Nest is a server-side framework for Node, that uses modern language features (via TypeScript) to create efficient, reliable, and scalable apps. The popularity of Nest has been exploding lately as developers are discovering how easy and powerful it is to build server-side applications with it. Front end developers are finding it especially easy to dive into full-stack development with Nest, while backend developers from other frameworks are finding familiar features they expect from a top-class web framework. In this talk, we will get introduced to Nest and discover how to create a simple, yet functional, API with it.</p> <p>---</p> <p>Speaker:</p> <p>Ely Lucas<br/>@elylucas</p> <p>Ely is a web and mobile developer living in Denver, CO with his wife, four kids, and dog. He works at Ionic as a software engineer and developer advocate and loves all things web. When not coding, he enjoys learning new tech and sharing with others.</p> <p>---</p> <p>Schedule:</p> <p>6:30pm: Pizza, beer, networking<br/>7:00pm: Talk<br/>7:45pm: Questions</p> <p>Hope you all can make it out to this great talk. If you have any questions, don\'t hesitate to reach out.</p> ',
